<template>
  <transition name="slide">
    <div class="wrapper">
      <div class="header">
        <i class="back" @click="back"></i>
        <span class="text">修改地址</span>
      </div>
      <div class="content-wrapper">
        <ul class="content">
          <li class="item">
            <label>收货人</label>
            <div class="input-wrapper">
              <input type="text" v-model="name" ref="name" />
            </div>
          </li>
          <li class="item">
            <label>联系电话</label>
            <div class="input-wrapper">
              <input type="text" v-model="phone" ref="phone" />
            </div>
          </li>
          <li class="item">
            <label>收货地址</label>
            <div class="input-wrapper">
              <input type="text" v-model="address" ref="address" />
            </div>
          </li>
        </ul>
        <div class="btn-wrapper">
          <span class="btn" @click="confirm">保存</span>
        </div>
      </div>
      <alert ref="alert" :message="alertMsg"></alert>
    </div>
  </transition>
</template>

<script type="text/ecmascript-6">
  import Alert from 'base/alert/alert'
  import {mapGetters,mapActions} from 'vuex'

  export default {
    data () {
      return {
        name: '',
        phone: '',
        address: '',
        alertMsg: ''
      }
    },
    created () {

    },
    computed: {
      ...mapGetters([
        'addressInfo'
      ])
    },
    mounted () {
      this.setName()
      this.setPhone()
      this.setAddress()
    },
    methods: {
      back() {
        this.$router.back()
      },
      setName() {
        this.name = this.addressInfo.name
      },
      setPhone() {
        this.phone = this.addressInfo.phone
      },
      setAddress() {
        this.address = this.addressInfo.address
      },
      confirm() {
        let name = this.name
        let phone = this.phone
        let address = this.address
        if(!name){
          this.alertMsg = '请输入收货人'
          this.showAlert()
          return
        }
        if(!phone){
          this.alertMsg = '请输入联系电话'
          this.showAlert()
          return
        }
        if(!address){
          this.alertMsg = '请输入收货地址'
          this.showAlert()
          return
        }
        this.setAddressInfo({
          name:this.name,
          phone:this.phone,
          address:this.address
        })
        this.$router.back()
      },
      showAlert() {
        this.$refs.alert.show()
      },
      ...mapActions([
        'setAddressInfo'
      ])
    },
    components: {
      Alert
    }
  }
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  @import "~common/stylus/variable"

  .slide-enter,.slide-leave-to
    transform translate3d(100%,0,0)

  .slide-enter-active,.slide-leave-active
    transition all 0.3s

  .wrapper
    position absolute
    top 0
    left 0
    right 0
    bottom 0
    background $color-background
    z-index 1
    .header
      position fixed
      height 44px
      top 0
      left 0
      right 0
      background $color-header-background
      display flex
      justify-content center
      align-items center
      .back
        position absolute
        background url("~common/image/back.png") no-repeat
        background-size contain
        width 8px
        height 15px
        left 15px
        top 15px
        &:before
          position absolute
          left -10px
          right -10px
          top -10px
          bottom -10px
          content ''
      .text
        color $color-header-text
        font-size $font-size-large
        font-weight bold
    .content-wrapper
      position fixed
      left 0
      right 0
      top 44px
      bottom 0
      .content
        display flex
        flex-direction column
        height 192px
        width 100%
        margin-top 10px
        background #fff
        .item
          display flex
          flex 1
          align-items center
          margin-left 15px
          margin-right 15px
          font-size $font-size-medium
          color #333
          label
            display flex
            width 90px
          .input-wrapper
            display flex
            flex 1
            align-items center
            height 100%
            input
              display block
              width 100%
              height 100%
              box-sizing border-box
              padding-left 15px
              padding-right 15px
              outline none
      .btn-wrapper
        margin-top 30px
        display flex
        justify-content center
        .btn
          width 280px
          height 40px
          display flex
          align-items center
          justify-content center
          color #fff
          background #EB4747
          border-radius 5px
          font-size $font-size-large

</style>



